import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  private name: string;
  private age: number;
  private email: string;
  private skills: string[];
  private address: {
    street: string,
    city: string,
    province: string,
    postcode: string
  }

  private todolist: todo[];
  private isEditable: boolean = true;
  private isEdit: string = "hide";

  //--------------------------------------------------------------------------------------Test lottery
  private lotterys: lottery[];

  constructor(private httpService: HttpService) {

  }

  ngOnInit() {
    this.name = "Setthawut Arunbanphod";
    this.age = 22;
    this.email = "setthawut@gmail.com";
    this.address = {
      city: "Pisanulok",
      postcode: "65000",
      province: "Pisanulok",
      street: "43/8 M.8",
    }
    this.skills = ["c++", "python", "c#", "assembly"];

    this.httpService.getTodoList().subscribe((res) => {
      this.todolist = res;
    })


    /* this.httpService.getLotteryList().subscribe((res)=>{
      this.lotterys = res;
      console.log("this.lotterys",this.lotterys);
    }) */

  }

  toggle() {
    this.isEditable = !this.isEditable;
    if (this.isEdit == "show") {
      this.isEdit = "hide";
    }
    else {
      this.isEdit = "show";
    }
  }

  addSkill(skill) {
    this.skills.unshift(skill);
    return false;
  }

  removeSkill(skill) {
    this.skills.forEach((element, index) => {
      if (element == skill) {
        this.skills.splice(index, 1);
      }
    });
  }

}

interface todo {
  userId: number;
  id: number;
  title: string;
  completed: boolean;
}

interface lottery {
  title: string;
  prize1: string;
  prizelast2: string;
  prizelast3: string;
  prizefirst3: string;
  prizenear: string;
  prize2: string;
  prize3: string;
  prize4: string;
  prize5: string;
}