import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class CarService {

  constructor(private http:Http) { }

  addCar(car){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://localhost:3000/cars/addcar',car,{headers:headers}).pipe(map((res)=>res.json()));
  }
  
  findCar(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://localhost:3000/cars/findcar',user,{headers:headers}).pipe(map((res)=>res.json()));
  }
}
