import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { UserComponent } from './components/user/user.component';
import { FormsModule } from '@angular/forms'
import { HttpService } from './services/http.service'
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { ProfileComponent } from './components/profile/profile.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { TabsleftComponent } from './components/tabsleft/tabsleft.component';
import { NodesComponent } from './components/nodes/nodes.component';
import { MockService } from './services/mock.service';
import { ValidateService } from './services/validate.service';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { AuthService } from './services/auth.service';
import { CarService } from './services/car.service';
import { NodemcuService } from './services/nodemcu.service';
import { PopupModule } from 'ng2-opd-popup';

import { environment } from '../environments/environment';
import { AngularFireModule } from '@angular/fire';
import { FirebaseService } from './services/firebase.service';
import { AngularFireDatabaseModule } from '@angular/fire/database';
import { TestdataComponent } from './components/testdata/testdata.component';

import { AgmCoreModule } from '@agm/core';
import { SettingComponent } from './components/setting/setting.component';
import { TestmapComponent } from './components/testmap/testmap.component'

const appRoutes: Routes = [
  { path: "", component: LoginComponent },
  { path: "login", component: LoginComponent },
  { path: "register", component: RegisterComponent },
  { path: "dashboard", component: DashboardComponent },
  { path: "profile", component: ProfileComponent },
  { path: "tabsleft", component: TabsleftComponent },
  { path: "nodes/:car_id/:car_name", component: NodesComponent },
  { path: "setting", component: SettingComponent },

  { path: "testdata", component: TestdataComponent },
  { path: "testmap", component: TestmapComponent },

  { path: "lottery", component: UserComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    UserComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    ProfileComponent,
    DashboardComponent,
    TabsleftComponent,
    NodesComponent,
    TestdataComponent,
    SettingComponent,
    TestmapComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes,{useHash:true}),
    FlashMessagesModule,
    FlashMessagesModule.forRoot(),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    AgmCoreModule.forRoot({
      apiKey: environment.GoogleMapApiKey
    }),
    PopupModule.forRoot()
  ],
  providers: [
    HttpService,
    MockService,
    ValidateService,
    AuthService,
    CarService,
    NodemcuService,
    FirebaseService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
