import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  authtoken:any;
  user:any;

  constructor(
    private http:Http
    ) { }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://178.128.92.198:3000/users/register',user,{headers:headers}).pipe(map((res)=>res.json()));
  }

  authenticationUser(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://178.128.92.198:3000/users/auth',user,{headers:headers}).pipe(map((res)=>res.json()));
  }

  storeUserData(token,user){
    localStorage.setItem('id_toekn',token);
    localStorage.setItem('user',JSON.stringify(user));
    this.authtoken = token;
    this.user = user; 
  }

  
}
