import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';

@Injectable({
  providedIn: 'root'
})
export class FirebaseService {

  constructor(private firebsedb: AngularFireDatabase) { }

  //------------------------------------------------------------------------------------- Set update and get car data
  addCar(car) {
    this.firebsedb.object('/car/' + car.car_id).update({
      car_id: car.car_id,
      car_name: car.car_name,
      username: car.username
    });
  }

  updateCarByCar_id(car) {
    this.firebsedb.object('/car/' + car.car_id).update({
      line_id: car.line_id,
      line_token: car.line_token,
      high_temp: car.high_temp,
      low_temp: car.low_temp
    });
  }

  findCarByUsername(username) {
    return this.firebsedb.list('/car', ref => ref.orderByChild('username').equalTo(username)).valueChanges();
  }

  findCarByCar_id(car_id) {
    return this.firebsedb.list('/car', ref => ref.orderByChild('car_id').equalTo(car_id)).valueChanges();
  }

  //------------------------------------------------------------------------------------- Set new nodemcu on firebase
  setNodemcutoFirebase(nodemcu) {
    this.firebsedb.list('car').query.orderByChild('car_id').equalTo(nodemcu.car_id).once('value', res => {
      res.forEach(data => {
        const car = data.val();
        if (car.high_temp != undefined && car.low_temp != undefined) {
          this.firebsedb.object('/nodemcu/' + nodemcu.nodemcu_id).update({
            nodemcu_id: nodemcu.nodemcu_id,
            nodemcu_name: nodemcu.nodemcu_name,
            car_id: nodemcu.car_id,
            high_temp: car.high_temp,
            low_temp: car.low_temp
          });
        }
        else {
          this.firebsedb.object('/nodemcu/' + nodemcu.nodemcu_id).update({
            nodemcu_id: nodemcu.nodemcu_id,
            nodemcu_name: nodemcu.nodemcu_name,
            car_id: nodemcu.car_id
          });
        }
      });
    });
  }


  findNodemcuBycar_id(car_id) {
    return this.firebsedb.list('/nodemcu', ref => ref.orderByChild('car_id').equalTo(car_id)).valueChanges();
  }

  updateNodemcu(car) {
    this.firebsedb.list('nodemcu').query.orderByChild('car_id').equalTo(car.car_id).once('value', res => {
      res.forEach(data => {
        const nodemcu = data.val();
        this.firebsedb.object('/nodemcu/' + nodemcu.nodemcu_id).update({
          high_temp: car.high_temp,
          low_temp: car.low_temp
        });
      })
    })
  }

  //--------------------------------------------------------------------------------------- Remove car
  removeCarBycar_id(car_id) {
    this.firebsedb.object('/car/' + car_id).remove();
  }

  //--------------------------------------------------------------------------------------- Remove nodemcu
  removeNodemcuBynodemcu_id(nodemcu_id) {
    this.firebsedb.object('/nodemcu/' + nodemcu_id).remove();
  }

  //--------------------------------------------------------------------------------------- Set new gps on firebase
  setGpstoFirebase(car) {
    this.firebsedb.object('/gps/' + car.car_id).update({
      car_id: car.car_id,
      latitude: 13.702394,
      longitude: 100.525895
    });
  }

  findGpsBycar_id(car_id) {
    return this.firebsedb.list('/gps', ref => ref.orderByChild('car_id').equalTo(car_id)).valueChanges();
  }

  //----------------------------------------------------------------------------------------- Tetst Data
  testdata(nodemcu) {
    this.firebsedb.object('/nodemcu/' + nodemcu.nodemcu_id).update({
      temperature: nodemcu.temperature,
      humidity: nodemcu.humidity
    });
  }

  findGpsTestMap(testDelay) {
    return this.firebsedb.list('gps/' + testDelay).valueChanges();
  }


}
