import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { FirebaseService } from '../../services/firebase.service';
import { Observable } from 'rxjs';
import { Popup } from 'ng2-opd-popup';
import { Router } from '@angular/router';

//import { AgmCoreModule } from '@agm/core'

@Component({
  selector: 'app-nodes',
  templateUrl: './nodes.component.html',
  styleUrls: ['./nodes.component.css']
})


export class NodesComponent implements OnInit {

  constructor(
    private flashMessagesService: FlashMessagesService,
    private route: ActivatedRoute,
    private firebaseService: FirebaseService,
    private router:Router
  ) { }

  @ViewChild('removenodemcu') removenodemcu: Popup;
  @ViewChild('removecar') removecar: Popup;

  private nodemcu_id: string;
  private nodemcu_name: string;

  private user: any;
  private car_id: string;
  private car_name: string;

  getnodemcus: Observable<any[]>;
  carGpss: Observable<any[]>;

  private deletenodemcu: string;
  private deletecar: string;


  //--------------------------------------------------------------------------------Google map api
  marker_icon = {
    url: './assets/images/marker_icon.svg',
    scaledSize: {
      width: 60,
      height: 60
    }
  }
  ngOnInit() {
    //------------------------------------------------------------------------------Get User, car in paramMap
    this.user = JSON.parse(localStorage.getItem('user'));
    this.car_id = this.route.snapshot.paramMap.get('car_id');
    this.car_name = this.route.snapshot.paramMap.get('car_name');

    //Create structure of car
    const car = {
      username: this.user.username,
      car_id: this.car_id
    }

    //--------------------------------------------------------------------------------fins car in mongodb 
    this.carGpss = this.firebaseService.findGpsBycar_id(car.car_id);
    console.log("this.carGps", this.carGpss);


    //--------------------------------------------------------------------------------find nodemcu in mongodb
    this.getnodemcus = this.firebaseService.findNodemcuBycar_id(car.car_id); //---find data of nodemcu in firebase

  }

  //Add new nodemcu
  addNewnodemcu() {
    const nodemcu = {
      car_id: this.car_id,
      username: this.user.username,
      nodemcu_id: this.nodemcu_id,
      nodemcu_name: this.nodemcu_name
    }
    this.nodemcu_id = "";
    this.nodemcu_name = "";

    //------------------------------------------------------------------------------------Set new nodemcu on firebase
    this.firebaseService.setNodemcutoFirebase(nodemcu);

    return false;
  }

  //Remove car
  removeCar(car_id){
    this.removecar.options = {
      header: "Delete car!",
      color: "#4f4f4f", // red, blue....
      widthProsentage: 20, // The with of the popou measured by browser width
      animationDuration: 0, // in seconds, 0 = no animation
      showButtons: false, // You can hide this in case you want to use custom buttons
      confirmBtnContent: "OK", // The text on your confirm button
      cancleBtnContent: "Cancel", // the text on your cancel button
      confirmBtnClass: "btn btn-default", // your class for styling the confirm button
      cancleBtnClass: "btn btn-default", // you class for styling the cancel button
      animation: "fadeInDown" // 'fadeInLeft', 'fadeInRight', 'fadeInUp', 'bounceIn','bounceInDown'
    };
    this.deletecar = car_id;
    this.removecar.show(this.removecar.options);
    return false;
  }
  removecarOK(){
    this.firebaseService.removeCarBycar_id(this.deletecar);
    this.deletecar = "";
    this.removecar.hide();
    this.router.navigate(['/dashboard']);
  }
  removecarClose(){
    this.removecar.hide();
  }

  //Remove nodemcu
  removeNodemcu(nodemcu) {

    this.removenodemcu.options = {
      header: "Delete nodemcu!",
      color: "#4f4f4f", // red, blue....
      widthProsentage: 25, // The with of the popou measured by browser width
      animationDuration: 0, // in seconds, 0 = no animation
      showButtons: false, // You can hide this in case you want to use custom buttons
      confirmBtnContent: "OK", // The text on your confirm button
      cancleBtnContent: "Cancel", // the text on your cancel button
      confirmBtnClass: "btn btn-default", // your class for styling the confirm button
      cancleBtnClass: "btn btn-default", // you class for styling the cancel button
      animation: "fadeInDown" // 'fadeInLeft', 'fadeInRight', 'fadeInUp', 'bounceIn','bounceInDown'
    };
    this.deletenodemcu = nodemcu.nodemcu_id;
    this.removenodemcu.show(this.removenodemcu.options);
    return false;
  }
  removenodemcuOK() {
    this.firebaseService.removeNodemcuBynodemcu_id(this.deletenodemcu);
    this.deletenodemcu = "";
    this.removenodemcu.hide();
  }
  removenodemcuClose() {
    this.removenodemcu.hide();
  }
}
