import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class NodemcuService {

  constructor(private http:Http) { }

  addNodemcu(nodemcu){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://localhost:3000/nodemcus/addnodemcu',nodemcu,{headers:headers}).pipe(map((res)=>res.json()));
  }

  findNodemcu(car){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('http://localhost:3000/nodemcus/findnodemcu',car,{headers:headers}).pipe(map((res)=>res.json()));
  }
}
